<template>
  <div class="header" id="vheader">
    <div class="header-left">
      <img src="../../assets/img/logo.png">
    </div>
    <el-dropdown trigger="click" @command="handleCommand">
      <span class="el-dropdown-link1">
        同业资产负债管理平台
        <i class="el-icon-caret-bottom"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <a href="" target="_blank">
          <el-dropdown-item>dem01</el-dropdown-item>
        </a>
        <el-dropdown-item divided>dem02</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <div class="header-middle">
      <!-- 头部菜单 -->
      <ul class="header-menu">
        <li v-for="item in menu" :key="item.num" @click="handleSelect(item.num)">
          <p :class="curmenu==item.num?'mn-active':''">{{item.name}}</p>
          <div class="und-line">
            <hr class="hr" v-show="curmenu==item.num">
          </div>
        </li>
      </ul>
    </div>
    <div class="header-right">
      <!-- 用户头像 -->
      <div class="user-avator">
        <span class="el-icon-user-solid"></span>
      </div>
      <!-- 用户名下拉菜单 -->
      <el-dropdown trigger="click">
        <span class="el-dropdown-link">
          admin<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import {
  Menu,
  MenuItem,
  Submenu,
  Dropdown,
  DropdownMenu,
  DropdownItem
} from 'element-ui'
Vue.use(Menu)
Vue.use(MenuItem)
Vue.use(Submenu)
Vue.use(Dropdown)
Vue.use(DropdownMenu)
Vue.use(DropdownItem)

export default {
  data () {
    return {
      curmenu: '1',//当前选中菜单
      name: 'linxin',
      menu: [{ name: '首页', num: 1 }, { name: '工作台', num: 2 }]
      //{ name: '业务参数维护', num: 3 }, { name: '业务报表', num: 4 }, { name: '数据模型', num: 5 }, { name: '系统管理', num: 6 }, { name: '权限管理', num: 7 }
    }
  },
  computed: {
    username () {
      let username = localStorage.getItem('ms_username')
      return username ? username : this.name
    }
  },
  methods: {
    //头部菜单选择
    handleSelect (e) {
      this.curmenu = e;
      if (e == '1') {
        this.$store.commit('setHideLeft', true)
        this.$router.push({ path: '/home' });
      } else if (e == '2') {
        this.$router.push({ path: '/branchForecast' });
        this.$store.commit('setHideLeft', false)
      } else {
        this.$store.commit('setHideLeft', false)
      }
    },
    // 用户名下拉菜单选择事件
    handleCommand (command) {
      if (command == 'loginout') {
        localStorage.removeItem('ms_username')
        this.$router.push('/login')
      }
    }
  },
  mounted () {
    if (this.curmenu == '1') {
      this.handleSelect('1');
    }

  }
}
</script>
<style scoped lang="less">
#vheader {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  background: #ffffff;
  font-family: PingFangSC-Medium;
  padding-left: 10px;
  .header-menu {
    display: flex;
    li {
      padding: 0 10px;
      font-size: 14px;
      cursor: pointer;
      .und-line {
        display: flex;
        justify-content: center;
      }
      .hr {
        margin-top: 5px;
        border-top: solid 2px;
        width: 26px;
        color: #ea5404;
        transform: scale(0.6);
      }
      .mn-active {
        color: #ea5404;
      }
    }
  }
  .header-middle {
    padding-left: 30px;
    margin-top: 5px;
  }
  .header-right {
    display: flex;
    justify-content: right;
    position: absolute;
    right: 20px;
  }
  .el-menu-item {
    padding: 0 10px;
  }
  .el-dropdown {
    cursor: pointer;
  }
}
.el-menu.el-menu--horizontal {
  border-bottom-style: none;
}
.header-user-con {
  display: flex;
  height: 59px;
  align-items: center;
}
.el-icon-user-solid {
  color: #ea5404;
}
.btn-bell-badge {
  position: absolute;
  right: 0;
  top: -2px;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background: #f56c6c;
  color: #fff;
}
.btn-bell .el-icon-bell {
  color: #fff;
}
.user-avator {
  margin-left: 20px;
}
.user-avator img {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-dropdown-link1 {
  cursor: pointer;
  color: #333333;
  font-size: 18px;
}
.el-dropdown-link2 {
  cursor: pointer;
  color: #333333;
  font-size: 14px;
}
</style>
