<template>
  <div class="sidebar" id="sidebar">
    <el-menu class="sidebar-el-menu" :default-active="onRoutes" :collapse="collapse" background-color="#FFFFFF" text-color="#333333" active-text-color="rgb(255, 208, 75)" unique-opened router>
      <template v-for="item in items">
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu v-if="subItem.subs" :index="subItem.index" :key="subItem.index">
                <template slot="title">{{ subItem.title }}</template>
                <el-menu-item v-for="(threeItem, i) in subItem.subs" :key="i" :index="threeItem.index">{{ threeItem.title }}</el-menu-item>
              </el-submenu>
              <el-menu-item v-else :index="subItem.index" :key="subItem.index">{{ subItem.title }}</el-menu-item>
            </template>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
export default {
  data () {
    return {
      collapse: false,
      items: [
        {
          icon: "el-icon-bank-card",
          index: "3",
          title: "月末预报",
          subs: [
            {
              index: "branchForecast",
              title: "分行报告"
            }
          ]
        },
        {
          icon: "el-icon-notebook-2",
          index: "6",
          title: "交易审批申请",
          subs: [
            {
              index: "transactionApproval",
              title: "交易审批申请"
            }
          ]
        },
      ]
    };
  },
  computed: {
    onRoutes (e) {
      console.info(e);
      if (this.$route.path == '/transactionApproval') {
        this.$store.commit('setHideLeft', true)
      }
      return this.$route.path.replace("/", "");
    }
  }
};
</script>
<style scoped lang="less">
#sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 0;
  overflow-y: scroll;
  padding: 10px;
  background: rgba(244, 244, 243);
  z-index: 9;
  ul {
    height: 100%;
  }
  .sidebar-el-menu:not(.el-menu--collapse) {
    width: 200px;
  }
  .el-menu-item.is-active {
    color: #ea5404 !important;
    background: #fff5f0 !important;
  }
  .el-menu {
    border-right-style: none;
  }
  .el-submenu [class^='el-icon-'] {
    color: #ea5404;
  }
}
#sidebar::-webkit-scrollbar {
  width: 0;
}
</style>
