<template>
  <div class="wrapper">
    <v-head></v-head>
    <v-sidebar v-show="!isHideLeft"></v-sidebar>
    <div class="content-box" :class="isHideLeft?'content-collapse': 'un-content-collapse' ">
      <div class="content">
        <transition name="move" mode="out-in">
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import vHead from "./Header";
import vSidebar from "./Sidebar.vue";

export default {
  data () {
    return {
      isHideLeft: true,
      tagsList: [],
      collapse: false
    };
  },
  components: {
    vHead,
    vSidebar
  },
  computed: {
    hideLeft () {
      return this.$store.state.hideLeft
    }
  },
  watch: {
    hideLeft: function (n) {
      if (n) {
        this.isHideLeft = true
      } else {
        this.isHideLeft = false
      }
    }
  },
  created () { }
};
</script>
<style lang="less">
.content-collapse {
  left: 0;
}
.un-content-collapse {
  left: 216;
}
</style>
